.section-media-quotes > .inner-content {
  margin-left: auto;
  margin-right: auto;
}
#mediaQuotes {
  position: relative;
}
#mediaQuotes::after {
  content: "flickity";
  display: none;
}
.media-quotes-list::before,
.media-quotes-list::after {
  content: "";
  display: table;
  font-size: 0;
}
.media-quotes-list::after {
  clear: both;
}
.media-quote {
  color: #77767a;
  font-size: 14px;
  line-height: 20px;
  width: 100%;
}
.media-quote:link,
.media-quote:visited {
  color: #77767a;
}
.media-quote > .inner-content {
  padding: 120px 24px 0;
  position: relative;
  margin-left: auto;
  margin-right: auto;
  max-width: 432px;
}
.media-quote > .inner-content::before {
  background-image: url("data:image/svg+xml;base64,");
  background-position: center;
  content: "";
  display: block;
  height: 32px;
  left: 24px;
  position: absolute;
  top: 0;
  width: 32px;
}
.media-quote > .inner-content::after {
  background: transparent url("https://cloud.paysend.com/images/web/i/sprite-quotes2.png") no-repeat 0 0 scroll;
  background-size: 100% auto;
  content: "";
  height: 48px;
  left: 24px;
  position: absolute;
  top: 48px;
  width: 208px;
}
.media-quote.yahoo > .inner-content::after {
  background-position: 0 -94px;
}
.media-quote.thecourier > .inner-content::after {
  background-position: 0 -160px;
}
.media-quote.techcrunch > .inner-content::after {
  background-position: 0 -240px;
}
.media-quote.financialtimes > .inner-content::after {
  background-position: 0 -323px;
  height: 55px;
  top: 52px;
}
.media-quote.deloitte > .inner-content::after {
  background-position: 0 -406px;
  height: 28px;
  top: 66px;
}
.media-quote.usatoday > .inner-content::after {
  background-position: 0 -476px;
  height: 28px;
  top: 64px;
}
.media-quote.nextbillion > .inner-content::after {
  background-position: 0 -545px;
  height: 55px;
  top: 50px;
}
.media-quote.innovativefinance > .inner-content::after {
  background-position: 0 -650px;
  height: 51px;
  top: 52px;
}
.media-quote.ibsintelligence > .inner-content::after {
  background-position: 0 -750px;
  height: 55px;
  top: 52px;
}
@media screen and (min-width: 664px) {
  .section-media-quotes {
    padding: 0 64px;
  }
  .section-media-quotes > .inner-content {
    max-width: none;
  }
  .media-quote {
    font-size: 16px;
    line-height: 24px;
    width: 100%;
  }
  .media-quote > .inner-content {
    margin-right: 0;
    padding: 128px 0 0;
    max-width: none;
  }
  .media-quote > .inner-content::before,
  .media-quote > .inner-content::after {
    left: 0;
  }
}
@media screen and (min-width: 785px) {
  .section-media-quotes > .inner-content {
    max-width: 640px;
  }
  .media-quote > .inner-content {
    max-width: 640px;
  }
}
@media screen and (min-width: 1368px) {
  .section-media-quotes {
    padding: 0 160px;
  }
  .section-media-quotes > .inner-content {
    max-width: none;
  }
  .media-quote {
    width: 33.3333%;
  }
  .media-quote > .inner-content {
    margin-right: 80px;
    max-width: none;
  }
}
@media screen and (min-width: 1616px) {
  .section-media-quotes > .inner-content {
    max-width: 1280px;
  }
}
