* {
    border-color: transparent;
    box-shadow: none;
    outline-style: none
}

.hidden {
    display: none
}

.qr-code__block {
    text-align: center
}
[id=header] .inner-content .right {
    right: 64px
}

[id=header] .inner-content:after,[id=header] .inner-content:before {
    content: "";
    display: none;
    font-size: 0
}

[id=header] .inner-content:after {
    clear: both
}

.flickity-viewport {
    height: 100%;
    overflow: hidden;
    position: relative
}
.section-faq,.section-mastercard,.section-media-quotes,.section-mobile-apps,.section-why {
    content-visibility: auto
}

.field__label {
    left: 15px;
    right: 15px
}
::selection {
    background: #7733ff36!important;
}
/*
css animation Iban business-connect end
 */
.animatedBanks {
    animation: dancingBanks 6s cubic-bezier(0.65, 0, 0.45, 1) infinite;
}
@-moz-keyframes dancingBanks {
    0% {
        opacity: 0;
    }
    7% {
        opacity: 0;
    }
    42% {
        opacity: 1;
    }
    50% {
        opacity: 1;
    }
    57% {
        opacity: 1;
    }
    85% {
        opacity: 0;
    }
    100% {
        opacity: 0;
    }
}
@-webkit-keyframes dancingBanks {
    0% {
        opacity: 0;
    }
    7% {
        opacity: 0;
    }
    42% {
        opacity: 1;
    }
    50% {
        opacity: 1;
    }
    57% {
        opacity: 1;
    }
    85% {
        opacity: 0;
    }
    100% {
        opacity: 0;
    }
}

@-o-keyframes dancingBanks {
    0% {
        opacity: 0;
    }
    7% {
        opacity: 0;
    }
    42% {
        opacity: 1;
    }
    50% {
        opacity: 1;
    }
    57% {
        opacity: 1;
    }
    85% {
        opacity: 0;
    }
    100% {
        opacity: 0;
    }
}

@keyframes dancingBanks {
    0% {
        opacity: 0;
    }
    7% {
        opacity: 0;
    }
    42% {
        opacity: 1;
    }
    50% {
        opacity: 1;
    }
    57% {
        opacity: 1;
    }
    85% {
        opacity: 0;
    }
    100% {
        opacity: 0;
    }
}

.section-banks .bank-logos li figure > img[alt='bank'] {
    box-shadow: none;
}

